import { CardTitleAndValue } from '@components/common/styles';
import { Stack, Typography } from '@mui/material';
import React, { useContext } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { notateNumber } from '@utility/helpers';
const MsaTotalizer = ({}) => {
  const {
    state
  } = useContext(AppContext);
  return <>
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="MsaTotalizer.jsx">MSA Totalizer</Typography>
    <Stack direction={'row'} gap={1} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="MsaTotalizer.jsx">
      {Object.entries(state?.account?.msaTotalizer || {}).map(([stat, {
        name,
        value
      }], index) => {
        return <CardTitleAndValue key={stat} title={name} value={`${notateNumber(value)}%`} icon={`etc/MSA_${index}.png`}>
        </CardTitleAndValue>;
      })}
    </Stack>
  </>;
};
export default MsaTotalizer;